.book-list {
  float: left;
  list-style: none;
  padding: 0;
  width: 400px;
  margin-left: 20px; }

.book-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #458A8A;
  &__cover {
    width: 100px;
    height: 100px;
    background-image: url('../../assets/images/582710.jpg');
    background-repeat: no-repeat;
    background-position: center; }
  &__title {
    color: #165A5A; } }
.btn_delete-book {
